import { Component, EventEmitter, Input, Output } from '@angular/core';
import { SharedService } from '../../../../../../services/shared.service';
import * as _ from 'underscore';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';
import { CloseRegisterService } from '../../../../cashier/close-register/services/close-register.service';
import { CollectService } from '../../../../cashier/collect/services/collect.service';
import { RegisterSettlementService } from '../../register-settlements-service/register-settlement.service';
import { ActivatedRoute } from '@angular/router';
import { ViewUploadedFileComponent } from '../../../bank-deposits/view-uploaded-file/view-uploaded-file.component';

@Component({
  selector: 'app-session-bank-transfer',
  templateUrl: './session-bank-transfer.component.html',
  styleUrls: ['./session-bank-transfer.component.scss']
})
export class SessionBankTransferComponent {
  banks = [];
  payerName = '';
  currency = '';
  pageNo: number = 1;
  sort: number = 1;
  sessionId: number;
  searchText: string = '';
  formGroup: FormGroup;
  sessionData: any;
  fileDetails = { fileName: '', file: '' }
  details: any;
  totalAllRecordsCount: number;
  totalRecordsCount: number;
  @Input() isSettle: boolean;
  @Output() loadMainData = new EventEmitter<string>();
  constructor(
    public _registerSettlementService: RegisterSettlementService,
    private activeRoute: ActivatedRoute,
    public closeRegisterService: CloseRegisterService,
    public sharedService: SharedService,
    private _modalService: NgbModal,
    private fb: FormBuilder,
    private _collectService: CollectService,
  ) { }

  ngOnInit(): void {
    this.initForm();
    this.getBankAccounts();
    this.activeRoute.params.subscribe(params => {
      this.sessionId = params['sessionId'];
      this.getSessionBankTransfere();
    })
    console.log(this.isSettle, 'Is setle')
  }
  initForm() {
    this.formGroup = this.fb.group({
      collectionId: [''],
      paidAmountJod: [{ value: '', disabled: true }],
      bankId: [null, [Validators.required]],
      drawerName: ['', [Validators.required]],
      transfereDate: [null, [Validators.required]],
      transfereReference: [null, [Validators.required]],
      transfereAttachment: [null],
      attachment: [null],
    })
  }
  handleBankTransferDetails(item, content) {
    this.details = item;
    this.fileDetails.file = item?.transfereAttachment;
    this.fileDetails.fileName = this.getFilName(item?.transfereAttachment);
    this._modalService.open(content, { size: 'lg', centered: true });
  }
  getFilName(name) {
    if (name)
      return name?.split('/').pop()
  }
  viewFile() {
    const modalRef = this._modalService.open(ViewUploadedFileComponent, { size: 'md' });
    modalRef.componentInstance.file = this.fileDetails?.file;
  }
  getSessionBankTransfere() {
    let obj = `?sessionId=${this.sessionId}&search=${this.searchText}&pageNo=${this.pageNo - 1}&pageSize=6&sort=${this.sort || 1}`;
    this._registerSettlementService
      .getSessionBankTransfer(obj)
      .subscribe((response: any) => {
        if (response) {
          this.sessionData = response.data;
          this.totalAllRecordsCount = response?.totalAllRecordsCount;
          this.totalRecordsCount = response?.totalRecordCount;
        }
      });
  }

  pageChange(page: number) {
    this.pageNo = page;
    this.getSessionBankTransfere();
    window.scroll(0, 0);
  }

  searchData(event: any) {
    const text = event.target.value;
    if (text.length >= 1) {
      this.searchText = text;
      this.pageNo = 1;
      this.getSessionBankTransfere();
    }
    if (text.length == 0) {
      this.pageNo = 1;
      this.getSessionBankTransfere();
    }
  }
  removeSearch() {
    this.searchText = '';
    this.pageNo = 1;
    this.getSessionBankTransfere();
  }

  actionsOnBankTransfer(item, type) {
    const formData = new FormData();
    formData.append('CollectionId', item.orderBankTransfereCollectionId);
    formData.append('Action', type);
    return this._registerSettlementService
      .actionsOnBankTransfer(formData)
      .subscribe((response: any) => {
        if (response.isSuccess) {
          this.getSessionBankTransfere();
        } else {
          console.log('error', response);
        }
      });
  }
  reverseActionOnBankTransfer(item) {
    const formData = new FormData();
    formData.append('OrderstransfereCollectionId', item.orderBankTransfereCollectionId);
    return this._registerSettlementService
      .reverseActionsOnBankTransfer(formData)
      .subscribe((response: any) => {
        if (response.isSuccess) {
          this.getSessionBankTransfere();
        } else {
          console.log('error', response);
        }
      });
  }

  openModalEdit(item, content) {
    console.log(item)
    this.fileDetails.file = item?.transfereAttachment;
    this.fileDetails.fileName = this.getFilName(item?.transfereAttachment);
    this.payerName = item?.cardPayerName;
    this.currency = item?.currency[0]?.lookupName;
    let obj = {
      collectionId: item?.orderBankTransfereCollectionId,
      paidAmountJod: (item?.paidAmountJod).toFixed(3),
      bankId: item?.bankAccount?.accountId,
      transfereDate: item?.transfereDate.split('T')[0],
      transfereReference: item?.transfereReference,
      drawerName: item?.cardPayerName,
    }
    this.formGroup.patchValue(obj);
    this._modalService.open(content, {
      centered: true, size: 'lg'
    });
  }
  onSubmit() {
    if (this.formGroup.invalid) {
      this.formGroup.markAllAsTouched();
    }
    else {
      let values = this.formGroup.value;
      const formData = new FormData();
      formData.append('CollectionId', values?.collectionId);
      formData.append('BankId', values?.bankId);
      formData.append('DrawerName', values?.drawerName);
      formData.append('TransfereDate', values?.transfereDate);
      formData.append('TransfereReference', values?.transfereReference);
      this._registerSettlementService.editCollectionBankTransfer(formData).subscribe((response) => {
        console.log(response);
        if (response.isSuccess) {
          this._modalService.dismissAll();
          this.successModal('success', 'Data saved successfully!')
          this.getSessionBankTransfere();
        }
      }, (error) => {
        this.successModal('error', error.error['errors']);
      })
    }
  }
  successModal(type, message) {
    const modelRef = this._modalService.open(ModalMessageComponent, {
      size: 'md'
    })
    modelRef.componentInstance.type = type;
    if (type == 'success') {
      modelRef.componentInstance.message = message;
    }
    else if (type == 'error') {
      modelRef.componentInstance.messageError = message;
    }
  }

  getBankAccounts() {
    let params = `?AccountType=15001&currencyId=9001&Status=2001&PageNo=0&PageSize=1000`
    this._collectService.getBankAccounts(params).subscribe((response: any) => {
      if (response.isSuccess) {
        this.banks = response['data'].map(x => { return { id: x.accountId, name: x.bankNameEnglish, accountName: x.accountName, accountNumber: x.accountNumber } });
      }
    })
  }
  sameCustomer(event) {
    let value = event?.target?.checked;
    if (value)
      this.formGroup.get('drawerName').setValue(this.payerName)
    else
      this.formGroup.get('drawerName').setValue('');
  }
  //SORTING
  sortByCustomer() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getSessionBankTransfere();
  }
  sortByReference() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getSessionBankTransfere();
  }
  sortByAmount() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getSessionBankTransfere();
  }
  sortByDate() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getSessionBankTransfere();
  }
  sortByBank() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getSessionBankTransfere();
  }
  sortByStatus() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.getSessionBankTransfere();
  }

}
