import { formatDate } from '@angular/common';
import { Component, ElementRef, EventEmitter, Input, Output, SimpleChanges, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import * as _ from 'underscore';
import { size } from 'underscore';
import { CommonService } from '../../../../../../services/common.service';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';
import { CloseRegisterService } from '../../../../cashier/close-register/services/close-register.service';
import { ViewFileComponent } from '../../../../cashier/collect/payment-modal/view-file/view-file.component';
import { ViewUploadedFileComponent } from '../../../bank-deposits/view-uploaded-file/view-uploaded-file.component';
import { RegisterSettlementService } from '../../register-settlements-service/register-settlement.service';

@Component({
  selector: 'app-session-cheques',
  templateUrl: './session-cheques.component.html',
  styleUrls: ['./session-cheques.component.scss']
})
export class SessionChequesComponent {
  pageNo: number = 1;
  dcPageNo: number = 1;
  sort: number = 1;
  pagin!: number;
  pages!: number[];
  searchText: string = '';
  @Input() response;
  sessionCheques: any;
  depositedCheques = [];
  dcTotalRecordCount = 0;
  sessionId = 0;
  chequeDetails: any;
  editCollectionResponse: any;
  bankList: any;
  totalRecordCount: number;
  @Output() loadData = new EventEmitter<any>();
  chequeForm: FormGroup;
  @Input() isSettle = true;
  canManageAndView: boolean;
  //file
  uploadedFile;
  enter: boolean;
  fileName = '';
  invalidFileType = '';
  @ViewChild('fileInput') inputFile: ElementRef<any>;
  constructor(private fb: FormBuilder, private _registerSettlementService: RegisterSettlementService, private _activeRoute: ActivatedRoute,
    private _modalService: NgbModal, private _closeRegisterService: CloseRegisterService, public _commonService: CommonService) {
  }
  ngOnChanges(changes: SimpleChanges) {
    this.response = changes['response'].currentValue;
    this.sessionCheques = this.response?.data;
    this.totalRecordCount = this.response?.totalRecordCount;
    this.pagin = Math.ceil(this.totalRecordCount / 6);
    this.pages = _.range(this.pagin);
  }
  ngOnInit() {
    this._activeRoute.params.subscribe(param => {
      this.sessionId = param['sessionId'];
      this.getDepositedCheques()
    })
    this.initChequeForm();
    this.canManageAndView = JSON.parse(localStorage.getItem('cmav'));
  }
  initChequeForm() {
    this.chequeForm = this.fb.group({
      chequeCollectionId: ['', [Validators.required]],
      chequeCollectionJod: [{ value: '', disabled: true }, [Validators.required]],
      chequeDate: ['', [Validators.required]],
      bankId: [null, [Validators.required]],
      chequeDrawerName: ['', [Validators.required]],
      chequeNo: ['', [Validators.required]]
    })
  }
  get f() {
    return this.chequeForm.controls;
  }
  getDepositedCheques() {
    let params = `?sessionId=${this.sessionId}&pageNo=${this.dcPageNo - 1}&pageSize=6`;
    this._registerSettlementService.getSessionDepositedCheques(params).subscribe(response => {
      console.log(response);
      if (response?.data?.length) {
        this.depositedCheques = response?.data;
        this.dcTotalRecordCount = response?.totalRecordCount;
      }
    })
  }
  viewDepositedAttachment(file) {
    const modalRef = this._modalService.open(ViewUploadedFileComponent);
    modalRef.componentInstance.file = file;
  }
  dcPageChange(page: number) {
    this.dcPageNo = page;
    this.getDepositedCheques();
  }
  handleChequeDetails(item, content) {
    this.chequeDetails = {};
    this.chequeDetails = item;
    console.log(this.chequeDetails);
    this._modalService.open(content, { centered: true, size: 'lg' });
  }
  handleEditAction(content, item) {
    console.log(item);
    this.getBanks();
    this.chequeDetails = {};
    this.chequeDetails = item;
    let obj = {
      chequeCollectionId: item.chequeCollectionId,
      chequeCollectionJod: item?.chequeCollectionJod,
      chequeDate: formatDate(item?.chequeDate, "yyyy-MM-dd", "en"),
      bankId: item?.bank[0]?.lookupId,
      chequeNo: item?.chequeNo,
      chequeDrawerName: item?.chequeCustomer,
    }
    this.chequeForm.patchValue(obj);
    //this.chequeForm.controls['chequeDrawerName'].setValue('');
    const modalRef = this._modalService.open(content, { centered: true, size: 'lg' });
    modalRef.result.then(x => {
      if (x === 'yes') {
        this.uploadedFile = null;
        this.fileName = '';
      }
    }, reject => {

    })
  }
  handleCheckBoxChange(event) {
    let value = event.target.checked;
    if (value) {
      this.chequeForm.controls['chequeDrawerName'].setValue(this.chequeDetails?.chequeCustomer);
    }
    else {
      this.chequeForm.controls['chequeDrawerName'].setValue('');
      this.chequeForm.controls['chequeDrawerName'].markAsTouched();
    }
  }
  handleSubmit() {
    if (this.chequeForm.valid) {

      let formData = new FormData();
      formData.append('chequeCollectionId', this.f['chequeCollectionId'].value);
      formData.append('chequeDate', this.f['chequeDate'].value);
      formData.append('bankId', this.f['bankId'].value);
      formData.append('chequeDrawerName', this.f['chequeDrawerName'].value);
      formData.append('chequeNo', this.f['chequeNo'].value);
      this._registerSettlementService.editCollectionCheuqe(formData).subscribe(response => {
        this.editCollectionResponse = response;
        if (this.uploadedFile?.file) {
          this.addSettleCheckImage();
        }
        this._modalService.dismissAll();
        this.successModal('success', 'Data saved successfully!')
        this.loadData.emit({ page: this.pageNo, chequeNumber: '', action: true })
        //this._modalService.open(this.successModal, { centered: true, })
      })
    }
    else {
      this.chequeForm.markAllAsTouched();
    }
  }
  addSettleCheckImage() {
    let formData = new FormData();
    formData.append('chequeCollectionId', this.f['chequeCollectionId'].value);
    formData.append('Image', this.uploadedFile?.file);
    this._registerSettlementService.addSettledChequesImages(formData).subscribe(response => {
      if (response.isSuccess) {
        this.uploadedFile = null;
        this.fileName = '';
      }
    })
  }
  successModal(type, message) {
    const modelRef = this._modalService.open(ModalMessageComponent, {
      size: 'md'
    })
    modelRef.componentInstance.type = type;
    if (type == 'success') {
      modelRef.componentInstance.message = message;
    }
    else if (type == 'error') {
      modelRef.componentInstance.messageError = message;
    }
  }
  handleActionClick(action, chequeCollectionId) {
    let formData = new FormData();
    formData.append('action', action);
    formData.append('chequeCollectionId', chequeCollectionId);
    this._registerSettlementService.actionOnCheque(formData).subscribe(response => {
      this.loadData.emit({ page: this.pageNo, chequeNumber: '', action: true })
    })
  }
  hadleReverseActionOnCheque(chequeCollectionId) {
    let formData = new FormData();
    formData.append('chequeCollectionId', chequeCollectionId);
    this._registerSettlementService.reverseActionOnCheque(formData).subscribe(response => {
      this.loadData.emit({ page: this.pageNo, chequeNumber: '' })
    })
  }
  getBanks() {
    this._closeRegisterService.getLookupsById(11).subscribe((response: any) => {
      this.bankList = response.data.map((x) => ({
        id: x.id,
        name: x.name[0].lookupName,
      }));
    });
  }
  sortByCustomer() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByChequeNo() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByAmount() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByDate() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByBank() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByStatus() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  pageChange(page: number) {
    this.pageNo = page;
    this.loadData.emit({ page, chequeNumber: '', sort: 1 });
    window.scroll(0, 0);
  }
  removeSearch() {
    this.searchText = '';
    this.loadData.emit({ page: 1, chequeNumber: '', sort: 1 })

  }
  searchData(event: any) {
    const text = event.target.value;
    if (text.length >= 3) {
      this.searchText = text;
      this.loadData.emit({ page: 1, chequeNumber: text, sort: 1 })
    }
    else if (text.length == 0) {
      this.searchText = '';
      this.loadData.emit({ page: 1, chequeNumber: '', sort: 1 })
    }
  }
  fileAction(action) {
    if (action == 'view') {
      const modelRef = this._modalService.open(ViewFileComponent, { size: 'md', })
      modelRef.componentInstance.imageUploadedView = this.uploadedFile;
    }
    else {
      //this.inputFile.nativeElement.value = null;
      (document.getElementById('chequeFile') as HTMLInputElement).value = null;
      this.fileName = '';
      this.uploadedFile = {};
    }
  }
  filesDropped(data: any) {
    this.enter = false;
    let isInvalid = this._commonService.checkInvalidImageFormat(data, true).invalidExtentions;
    this.invalidFileType = this._commonService.checkInvalidImageFormat(data, true).invalidError;
    if (isInvalid) {
      this.inputFile.nativeElement.value = null;
      return;
    }
    let files = data.map(x => {
      return x.file
    })
    if (!isInvalid) {
      files.forEach((file: File) => {
        this.fileName = file.name;
        this._commonService.fileToBase64(file).then(response => {
          this.uploadedFile = response;
        })
      })
    }
  }

  onSelectFile(event: any) {
    let files = [...event.target.files];
    let isInvalid = this._commonService.checkInvalidImageFormat(files, true).invalidExtentions;
    this.invalidFileType = this._commonService.checkInvalidImageFormat(files, true).invalidError;
    if (isInvalid) {
      this.inputFile.nativeElement.value = null;
      return;
    }
    if (files.length > 0) {
      if (!isInvalid) {
        files.forEach((file: File) => {
          this.fileName = file.name;
          this._commonService.fileToBase64(file).then(response => {
            this.uploadedFile = response;
          })
        })
      }
    }
  }
}
